<script>
import config from "../../../../public/config/customSetting.json";
export default {
  name: "censorAccount-component",
  data() {
    return {
      form: {
        work_Status: "", // 審核狀態
        description: "", // 註解
      },
      rules: {
        work_Status: [{ required: true, message: "必填", trigger: "blur" }],
      },
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            let res = await this.$store.dispatch("censorAccount", this.form);
            if (res.data.isOK) {
              this.$message({
                type: "success",
                duration: "5000",
                showClose: true,
                message: "審核成功",
              });
              // 清除審查表單
              // this.$refs[formName].resetFields();
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    async downloadHandler(form) {
      const fileName = form.file_Name.split(".")[0];
      const fileFormat = form.file_Name.split(".")[1];
      const res = await this.$store.dispatch("commonDownloadPdf", {
        file_id: form.file_id,
        router_Name: config.getVendorInfo,
      });
      this.downloadFile(res, fileName, fileFormat);
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得該筆明細
    const id = this.$route.params.id;
    try {
      const getAccCensorDataDetailRes = await this.$store.dispatch(
        "getAccCensorDataDetail",
        id
      );
      // 將明細內容覆寫入 form
      this.form = getAccCensorDataDetailRes.data.data.detail;
    } catch (err) {
      console.log(err);
    }
  },
};
</script>

<template src="./censorAccount.html"></template>
<style lang="sass" src="../../../../public/styles/custom.sass"></style>
